@charset "UTF-8";

html {
    height: 100%;
    font-size: 10px;
    font-family: Noto Sans, Noto Sans CJK KR, AppleSDGothicNeo-Medium, sans-serif;
    line-height: 1.25em;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased
}

body,
button,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
legend,
li,
ol,
p,
select,
table,
td,
textarea,
th,
ul {
    margin: 0;
    padding: 0
}

body,
button,
input,
select,
table,
textarea {
    font-family: Noto Sans, Noto Sans CJK KR, AppleSDGothicNeo-Medium, sans-serif;
    font-size: 1.4rem;
    line-height: 1.25em
}

body {
    height: 100%;
    -webkit-text-size-adjust: none;
    overflow-wrap: break-word
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
    display: block
}

audio,
canvas,
progress,
video {
    display: inline-block
}

audio:not([controls]) {
    display: none;
    height: 0
}

progress {
    vertical-align: baseline
}

[hidden],
template {
    display: none
}

a {
    background-color: transparent;
    -webkit-text-decoration-skip: objects
}

a,
a:hover {
    text-decoration: none
}

a:active,
a:hover {
    outline-width: 0
}

abbr[title] {
    border-bottom: none;
    text-decoration: underline;
    -webkit-text-decoration: underline dotted;
    text-decoration: underline dotted
}

b,
strong {
    font-weight: inherit;
    font-weight: bolder
}

dfn {
    font-style: italic
}

mark {
    background-color: transparent;
    color: #000
}

small {
    font-size: 80%
}

sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline
}

sub {
    bottom: -.25em
}

sup {
    top: -.5em
}

fieldset,
img {
    border-style: none;
    border: 0
}

svg:not(:root) {
    overflow: hidden
}

code,
kbd,
pre,
samp {
    font-family: monospace, monospace;
    font-size: 1em
}

button,
input,
optgroup,
select,
textarea {
    font: inherit;
    margin: 0
}

optgroup {
    font-weight: 700
}

button,
input {
    overflow: visible
}

button,
select {
    text-transform: none
}

button {
    padding: 0;
    border: 0;
    background-color: transparent;
    -webkit-appearance: none
}

[type=reset],
[type=submit],
button,
html [type=button] {
    cursor: pointer;
    -webkit-appearance: button
}

[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner,
button::-moz-focus-inner {
    border-style: none;
    border: 0;
    padding: 0
}

[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring,
button:-moz-focusring {
    outline: 1px dotted ButtonText
}

li,
ol,
ol li,
ul,
ul li {
    list-style: none
}

address,
em,
i {
    font-style: normal
}

table {
    border-collapse: collapse
}

textarea {
    overflow: auto;
    resize: none
}

[type=checkbox],
[type=radio] {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    height: auto
}

[type=search] {
    -webkit-appearance: textfield;
    outline-offset: -2px
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
    -webkit-appearance: none
}

::-webkit-input-placeholder {
    color: inherit;
    opacity: .54
}

::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit
}

button {
    outline: 0
}

hr {
    display: none
}

svg {
    vertical-align: top
}

a {
    color: inherit
}

img:not([src]) {
    opacity: 0
}

video.controls-hidden {
    width: 100%;
    min-width: 100%;
    height: auto
}

video.controls-hidden::-webkit-media-controls-panel,
video.controls-hidden::-webkit-media-controls-start-playback-button {
    display: none !important;
    -webkit-appearance: none
}

[aria-hidden=true] {
    display: none
}

.g-invisible,
.hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px)
}

.webview * {
    outline: 0 !important;
    -webkit-tap-highlight-color: transparent
}

* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box
}

.twd-agree-area {
    position: relative;
    padding: 30px;
    font-family: Malgun Gothic, 맑은고딕, 돋움, Dotum, sans-serif;
    font-size: 13px;
    line-height: 18px;
    color: #666
}

.twd-agree-area a {
    text-decoration: underline
}

.twd-agree-area p {
    margin-top: 5px;
    margin-bottom: 5px
}

.twd-agree-area .term-list,
.twd-agree-area h2 {
    margin-top: 10px;
    margin-bottom: 10px
}

.twd-agree-area h2 {
    margin-top: 40px;
    color: #333;
    font-size: 18px;
    font-weight: 700
}

.twd-agree-area h2:first-child {
    margin-top: 0
}

.twd-agree-area .list>li {
    margin-left: 18px;
    text-indent: -18px
}

.twd-agree-area .list li+li {
    margin-top: 10px
}

.twd-agree-area .list li li+li {
    margin-top: 7px
}

.twd-agree-area .list ol {
    margin-top: 5px
}

.twd-agree-area .list ul {
    margin-top: 5px;
    padding-left: 21px
}

.twd-agree-area .list>li>ol>li {
    position: relative;
    margin-left: 18px;
    text-indent: 0;
    list-style-type: decimal
}

.twd-agree-area .list>li>ol>li>ol>li {
    margin-left: 21px;
    text-indent: -21px
}